          transform: translate3d(calc(0% + -16px), 0, 0);
}
.tolt[data-microtip-position="left"]::before,
.tolt[data-microtip-position="left"]::after {
  bottom: auto;
  left: auto;
  right: 100%;
  top: 50%;
  -webkit-transform: translate3d(10px, -50%, 0);
