#progressbar {
	margin-bottom: 30px;
	counter-reset: step;
	width:100%;
	float:left;
	position:relative;
	box-sizing:border-box;
}
#progressbar li {
	list-style-type: none;
	color:#666;
	font-size:12px;
	width: 33.3%;
	float: left;
	position: relative;
}
#progressbar li:first-child {
	width:40px;
	position:absolute;
	left:0;
	top:0;
}
#progressbar li span {
	float:right;
	background:#e2e2e2;
	width:40px;
	height:40px;
	line-height:32px;
	color: #fff;
	border-radius:100%;
	position:relative;
	z-index:10;
	font-weight:500;
	border:4px solid #fff;
	box-sizing:border-box;
	transition: all 200ms linear;
	transition-delay: 0.2s;
	box-shadow: 0 2px 16px rgba(58, 87, 135, 0.15);
}
#progressbar li:after {
	content: '';
	position:absolute;
	bottom:50%;
	width:100%;
	margin-bottom:-2px;
	left:0;
	height:4px;
	border-radius:4px;
	background:#e8e8e8;
	z-index:1;
}
#progressbar li:before {
	content: '';
	position:absolute;
	bottom:50%;
	margin-bottom:-2px;
	width:0;
	left:0;
	height:4px;
	z-index:2;
	transition: all 200ms linear;
}
#progressbar li.active:before {
	width:100%;
}
