/*------Timeline------------------------------------------------*/
.process-wrap li {
	float:left;
	width:33.3%;
	padding:0 30px;
	position:relative;
}
.process-wrap_half li{
	width:50%;
}
.process-item {
	float:left;
	width:100%;
	position:relative;
	border:1px solid #eee;
	padding:30px 20px 50px;
	border-radius:6px;
	z-index:20;
	background:#f9f9f9;
}
.process-wrap li i {
	margin:0 auto 0;
	font-size:59px;
}
.process-wrap li h4 {
	text-align:center;
	float:left;
	width:100%;
	margin-bottom:12px;
	font-weight:600;
	font-size:18px;
	color: #566985;
}
.process-wrap p {
	text-align: center;
	color: #7d93b2;
	font-size:13px;
	line-height:22px;
}
.process-count {
	position:absolute;
	z-index:10;
	font-size:14px;
	font-weight:bold;
	left:50%;
	margin-left:-25px;
	bottom:-25px;
	background:#fff;
	border-radius:100%;
	width:50px;
	height:50px;
	line-height:50px;
	color:#666;
	border:1px solid #eee;
    box-shadow: 0 9px 16px rgba(58, 87, 135, 0.15);
}
.pr-dec {
	position:absolute;
	top:50%;
	right:-50px;
	width:150px;
	height:1px;
	border-bottom:1px solid #e2e2e2;
}
.process-end {
	display:inline-block;
	margin-top:60px;
	position:relative;
}
.process-end:before {
	content:'';
	position:absolute;
	top:-60px;
	left:50%;
	width:1px;
	height:70px;
	background:#e2e2e2;
	z-index:-1;
}
.process-end i {
	width: 60px;
    height: 60px;
    line-height: 60px;
    background: #fff;
    border-radius: 100%;
    z-index: 3;
    background: #5ECFB1;
    border-radius: 100%;
    font-size: 20px;
    color: #fff;
    border-radius: 100%;
	box-shadow: 0px 0px 0px 10px #f7f7f7
}
.hidden-section {
	overflow:hidden;
}
.process-item_time-line {
	margin-bottom:50px;
}
.process-item_time-line:before {
	content:'';
	position:absolute;
	left:0;
	right:0;
	height:2px;
	top:28px;
	z-index:1;
	background:#e9e9e9
}
.process-item_time-line:after {
	font-family: Font Awesome\ 5 Pro;
	content: "\f00c";
	position:absolute;
	width:40px;
	height:40px;
	line-height:40px;
	right:20px;
	top:8px;
	border-radius:100%;
	font-size:18px;
	color:#fff;
 	-webkit-animation: pulse3 3.1s infinite ease-in-out;
 	        animation: pulse3 3.1s infinite ease-in-out;
	z-index:2;
	display:none;
}
.process-item_time-line:last-child:after {
	display:block;
}
@-webkit-keyframes pulse3 {
    50% {
        box-shadow: 0 0 0 10px rgba(255,255,255,.2),
        0 0 0 20px rgba(255,255,255,.3),
        0 0 0 40px rgba(238, 238,238, .4);
    }
}
@keyframes pulse3 {
    50% {
        box-shadow: 0 0 0 10px rgba(255,255,255,.2),
        0 0 0 20px rgba(255,255,255,.3),
        0 0 0 40px rgba(238, 238,238, .4);
    }
}
