	padding-left:5px;
	font-weight:500;
}
.tfp-btn:before {
    font-family: Font Awesome\ 5 Pro;
    content: "\f0d7";
    position: absolute;
    color: #fff;
    top:50%;
    font-size: 13px;
    right: 11px;
	margin-top:-7px;
    font-weight: 800;
    transition: all .2s ease-in-out;
}
.tfp-btn.rot_tfp-btn:before {
	-webkit-transform: rotateX(180deg);
    transform: rotateX(180deg);
}
