.booking-list-message-text {
    margin-left: 30px;
    text-align: left;
    padding-left: 50px;
    top: 6px;
    position: relative;
}
.booking-details {
    padding: 6px 0;
    font-size: 12px;
}
.booking-title {
    color: #999;
    padding-right: 10px;
}
.booking-text, .booking-text a {
	color: #7d93b2;
}
.booking-list-message-text h4 {
    font-weight: 600;
    font-size: 14px;
    padding-bottom: 20px;
    color: #50596E;
}
.booking-list-message-text p , .profile-edit-container .reviews-comments-item-text p {
    text-align: left;
    color: #878C9F;
    font-size: 12px;
    line-height: 24px;
    padding-bottom: 10px;
}
.booking-list-contr {
	position:absolute;
	right:0;
	top:30px;
	z-index:10;
	padding:10px 20px;
	background:#f9f9f9;
	border: 1px solid #eee;
    border-right: none;
    box-sizing: border-box;
    border-radius: 38px 0 0 38px;
    box-shadow: 0 10px 15px rgba(0,0,0,0.03);
}
.booking-list-contr a {
	width:40px;
	height:40px;
	line-height:40px;
	color:#fff;
	float:left;
	position:relative;
 
	border-radius:100%;
	margin:0 4px;
}
.booking-list-new {
	position:absolute;
	left:30px;
	top:100px;
	padding:4px 12px;
	font-size:11px;
	color:#fff;
	border-radius:4px;
}
.dashboard-message .booking-list-contr {
	top:20px;
}
.dashboard-message-text img {
	float:left;
	width:90px;
	height:auto;
	border-radius:6px;
	box-shadow: 0 9px 16px rgba(58, 87, 135, 0.2);
	margin-right:20px;
}
.dashboard-message-text h4 {
	text-align:left;
	padding:8px 0 6px 0;
}
.dashboard-message-text h4 a {
	color: #566985;
}
.chat-box {
	padding:50px 30px 60px;
	min-height:300px;
}
.chat-message {
	padding-left:70px;
	margin-bottom:30px;
}
.chat-message:last-child {
	margin-bottom:0;
}
 
.message-input {
	float:left;
	width:100%;
	position:relative;
	border-right:1px solid #eee;
}
.message-input textarea{
	position:relative;
	width:100%;
	border:none;
	background:#fff;
	z-index:1;
	resize: vertical;
	min-height:60px;
	padding:25px 140px 0 30px;
}
.message-input button {
	position:absolute;
	right:20px;
	border-radius:36px;
	width:100px;
	height:36px;
	line-height:36px;
	background:none;
	border:none;
	font-size:14px;
	top:12px;
	z-index:2;
	color:#fff;
	cursor:pointer;
}
.chat-contacts-item {
	float:left;
	width:100%;
	position:relative;
	padding:20px  20px 20px 70px;
	border-bottom:1px solid #eee;
	box-sizing:border-box;
	background:#fff;
}
.chat-contacts-item:last-child {
	border-bottom:none;
}
.chat-contacts-item .dashboard-message-avatar{
	position:absolute;
	top:15px;
	left:0;
}
.chat-contacts-item .chat-contacts-item-text p {
    height: 26px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-size: 12px;
    color: #888;
}
.chat-contacts-item .chat-contacts-item-text h4 {
	text-align:left;
	font-size:12px;
	color:#666;
	padding-bottom:4px;
}
.chat-contacts-item .chat-contacts-item-text span {
	position:absolute;
	right:20px;
	top:21px;
	font-size:9px;
	color:#999;
}
.message-counter {
	position:absolute;
	left:0;
	bottom:-2px;
	z-index:10;
	width:20px;
	height:20px;
	line-height:20px;
	font-size:11px;
	color:#fff;
	border-radius:100%;
}
.nopadding {
   padding: 0 !important;
   margin: 0 !important;
}
.chat-contacts:before {
	content:'';
	position:absolute;
	left:-31px;
	width:1px;
	background:#eee;
	height:100%;
	top:0;
	z-index:10;
}
.chat-contacts-item-text {
	z-index:20;
	position: relative;
}
.chat-contacts-item:after {
	content:'';
	position:absolute;
	right:0;
	width:6px;
	height:0;
	z-index:2;
	top:0;
    transition: all .2s ease-in-out;
}
.chat-contacts-item_active:before {
	content:'';
	position:absolute;
	right:0;
	top:0;
	bottom:0;
	left:-30px;
	background:#ECF6F8;
 
}
.chat-contacts-item.chat-contacts-item_active:after , .chat-contacts-item:hover:after {
	height:100%;
}
.chat-contacts-item {
    float: left;
    width: 100%;
    position: relative;
    padding: 20px 20px 20px 70px;
    border-bottom: 1px solid #eee;
    box-sizing: border-box;
}
.chat-contacts-item .dashboard-message-avatar {
    position: absolute;
    top: 15px;
    left: 0;
}
.dashboard-message-avatar {
    float: left;
    width: 50px;
    height: 50px;
}
.chat-contacts-item .chat-contacts-item-text h4 {
    text-align: left;
    font-size: 12px;
    color: #666;
    padding-bottom: 4px;
}
.chat-contacts-item .chat-contacts-item-text span {
    position: absolute;
    right: 10px;
    top: 4px;
    font-size: 9px;
    color: #999;
}
.dashboard-message-avatar img {
    width: 50px;
    height: 50px;
    border-radius: 100%;
}
.message-counter {
    position: absolute;
    left: 0;
    bottom: -2px;
    z-index: 10;
    width: 20px;
    height: 20px;
    line-height: 20px;
    font-size: 11px;
    color: #fff;
    border-radius: 100%;
}
.chat-wrapper{
	background:#fff;
	border-radius:0 0 10px 10px;
 
}
.cmun_sm {
	top:50px;
}
.chat-wrapper .chat-widget_input {
	margin-top:30px;
	position:relative;
	width:100%;
	float:left;
	overflow:hidden;
	height: 60px;
}
.chat-wrapper .chat-widget_input textarea {
    padding: 17px 11px 11px;
}
.chat-wrapper  .chat-widget_input button {
	top:11px;
}
.dashboard-tabs .filter-sidebar-header {
    margin-bottom: 0;
    padding: 15px 20px 25px;
}
.follow-user-list {
	padding-bottom:20px;
}
.follow-user-list li{
	float:left;
	width:33.3%;
	margin-bottom:14px;
}
.follow-user-list li img {
	width:50px;
	height:50px;
	border-radius:100%;
    box-shadow: 0 9px 16px rgba(58, 87, 135, 0.2);
	transition: all .2s ease-in-out;
}
.follow-user-list li:hover img {
    box-shadow: 0 9px 16px rgba(58, 87, 135, 0);
}
.follow-user-list li span {
	display:block;
	padding-top:8px;
	color: #7d93b2;
	font-size:11px;
    transition: all .2s ease-in-out;
}
.dashboard-tabs .tabs-menu li a span {
	padding-left:4px;
}
