.user-profile-menu-wrap {
	display:none;
	margin-bottom:15px;
}
section.main-dashboard-sec {
    padding-top: 20px;
}
.add_new-dashboard {
	float:left;
}
.dashboard-header-stats-wrap {
	width:100%;
}
.dashboard-header_conatiner {
    padding-left: 0;
	padding-top:20px;
	padding-bottom:10px;
}
.dashboard-header-avatar {
    left: 20px;
