.dashboard-header  {
	padding:20px 0;
}
.dashboard-header_title {
	padding-left:110px;
	padding-bottom:0;
}
.tfp-btn {
	right:inherit;
	left:0;
	top:-40px;
}
.tfp-det {
	right:inherit;
	left:0;
 
}
.tfp-det.vis_tfp-det {
