}
@media only screen and  (max-width: 768px) {
.dashboard-message-text {
    width: 80%;
}
.dashboard-message-time {
	width:100%;
	padding:0;
	text-align:left;
	float:left;
}
.dashboard-message-text img {
	width:100%;
	margin-bottom:15px;
}
#myChartLegend {
    float: left;
}
#myChartLegend li {
	margin: 0 10px 0 0;
}
.booking-list-message-text {
	margin-left:0;
	padding-left:6px;
}
.booking-list-message-avatar {
	width:100%;
	margin-bottom:6px;
}
.booking-list-message-avatar img {
	float:left;
}
.booking-list-new {
    left: 90px;
    top: 50px;
}
}
